import React from 'react';

import './Project.scss';

function printButtons(project) {
    return (
        project.buttons.map((button, i) =>
            <div className="button-wrapper" key={i}>
                <a href={button.url} rel="noopener noreferrer" target="_blank">
                    <button>
                        {button.name}
                    </button>
                </a>
            </div>
        )
    )
}

function printProjectTopBottom(project) {
    return (
        <div className="card-content">
            <div className="top">
                {/*onClick={() => window.open(`${project.bg_image_url}`, "_blank")}*/}
                <div style={{content: `url(${project.backgroundImage})`}}/>
            </div>
            <div className="bottom">
                <div className="bottom-wrapper">
                    <div className="text-wrapper">
                        <h1 className="title">{project.name}</h1>
                        <p className="desc">{project.desc}</p>
                    </div>
                    <div className="action-buttons">
                        {printButtons(project)}
                    </div>
                </div>
            </div>
        </div>
    )
}

export function  ProjectFullscreen({project, turnOffFunc}) {
    return (
        <div className="project-card fullscreen">
            <button className="x-btn" onClick={turnOffFunc}>&#10006;</button>
            {printProjectTopBottom(project)}
        </div>
    )
}

export default function Project({project, id, onClick}) {
    return (
        <div className="project-card" onClick={() => onClick(id)}>
            {printProjectTopBottom(project)}
        </div>
    )
}
